import React, {PropTypes, Component} from 'react';
import {Field, reduxForm} from 'redux-form';
import Validator from '../utils/Validator';
import RenderField from './RenderField';
import Loading from '../components/Loading';

class RegisterForm extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        const {handleSubmit} = this.props;
        return (
            <form onSubmit={handleSubmit}>
                <div className="panel-body">
                    <Field type="text" name="name" component={RenderField}
                           label="H tn"/>
                    <Field type="text" component={RenderField} name="email"
                           label="Email"/>
                    <Field type="password" name="password"
                           component={RenderField}
                           label="Mt khu"/>
                    <Field type="password"
                           component={RenderField}
                           name="password_confirmation"
                           label="Xc nhn mt khu"/>

                    {
                        this.props.isProcessing ? <Loading/> :
                            <div className="form-group">
                                <button type="submit" className="btn btn-default">ng k</button>
                            </div>
                    }

                </div>

            </form>

        );
    }
}
const validate = values => {
    const errors = {};
    if (!values.name || values.name.trim() === '') {
        errors.name = "Bn vui lng nhp tn";
    }
    if (!values.email || values.email.trim() === '') {
        errors.email = 'Bn vui lng nhp email';
    } else if (!Validator.validateEmail(values.email)) {
        errors.email = 'Email cha ng nh dng';
    }
    if (!values.password || values.password.trim() === '') {
        errors.password = 'Bn vui lng nhp password';
    } else if (!Validator.validatePassword(values.password)) {
        errors.password = 'Mt khu cn c  di t nht 8 k t bao gm c S v Ch';
    } else if (values.password !== values.password_confirmation) {
        errors.password_confirmation = 'Mt khu v xc nhn cha khp';
    }
    return errors;
};

RegisterForm.propTypes = {
    isProcessing: PropTypes.bool.isRequired,
    handleSubmit: PropTypes.func.isRequired
};

export default reduxForm({
    form: "register",
    validate
})(RegisterForm);



